





























import {
  defineComponent, reactive, computed, ref, toRefs, watch,
} from '@vue/composition-api';
import OverText from '@/components/OverText.vue';
import useCols from './useCols';
import useWidthTransitionEnd from './useWidthTransitionEnd';

const fetchList = async (params:any) => {
  const listLength = params.isSearch ? 10 : 20;
  return [...Array(listLength)].map((_, i) => ({
    hid: `1_${i}`,
    title: `测试1_${i}`,
  }));
};

export default defineComponent({
  name: 'meet-home__review--list',
  components: { OverText },
  props: {
    colsNum: {
      type: Number as () => 4 | 8 | 16,
      default: 8,
    },
    searchValue: {
      type: String,
      default: '',
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      list: [] as any[],
      loading: false,
    });
    watch(() => ([props.searchValue, props.isSearch]), async () => {
      try {
        state.loading = true;
        state.list = await fetchList({ isSearch: props.isSearch });
      } finally {
        state.loading = false;
      }
    }, { immediate: true });
    const {
      currentColsNum, review, colWidth, colWidthStyle,
    } = useCols(props.colsNum);
    const colWrapStyle = computed(() => ({
      ...colWidthStyle.value,
      paddingRight: `${props.colsNum === 16 ? 10 : 30}px`,
    }));
    const { overText, handleWidthTransitionend } = useWidthTransitionEnd();
    const toCols = () => {
      emit('to-cols');
    };
    return {
      ...toRefs(state),
      toCols,
      currentColsNum,
      review,
      colWrapStyle,
      overText,
      handleWidthTransitionend,
    };
  },
});
