






















import {
  defineComponent, reactive, computed, ref, toRefs, onMounted, getCurrentInstance,
} from '@vue/composition-api';

export default defineComponent({
  name: 'over-text',
  inheritAttrs: false,
  components: {},
  props: {
    width: {
      type: [Number, String],
      default: 'auto',
    },
  },
  setup(props, { refs, emit, root }) {
    const _this = getCurrentInstance();
    const state = reactive({
      isOver: false,
      content: '',
      wrapStyle: {
        width: typeof props.width === 'string' ? props.width : `${props.width}px`,
      },
    });

    const setSlotsElInline = () => {
      // 这里的refs.wrap已经是isOver的wrap
      const slotEls = [...refs.wrap.children] as HTMLElement[];
      slotEls.forEach(el => {
        el.style.display = 'inline';
      });
    };

    // 可能从外部调用,不能修改名字
    const init = async () => {
      const WrapEl = refs.wrap as HTMLElement;

      if (WrapEl.scrollWidth > WrapEl.clientWidth) {
        state.isOver = true;
        state.content = WrapEl.textContent || '';

        await root.$nextTick();
        setSlotsElInline();
      } else {
        // 必须设置,有些场景可能从toolTip变成不需要toolTip
        state.isOver = false;
      }
    };

    onMounted(async () => {
      init();
    });

    return {
      ...toRefs(state),
      init,
    };
  },
});
