


























import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';
import Cols from './Cols.vue';
import List from './List.vue';
import SearchEmpty from './SearchEmpty.vue';

export default defineComponent({
  name: 'meet-home__review',
  components: {
    Cols, List, SearchEmpty,
  },
  props: {
    showCols: {
      type: Array as ()=>any[],
      default: () => [],
    },
    colsNum: {
      type: Number as ()=> 4|8|16,
      default: 8,
    },
    col: {
      type: Object,
      default: () => ({}),
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
    searchValue: {
      type: String,
      default: '',
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({});

    const openColList = (currentCol:any) => {
      emit('open-col-list', currentCol);
    };

    const toCols = () => {
      emit('to-cols');
    };


    return {
      ...toRefs(state),
      openColList,
      toCols,
    };
  },
});
